
#article-body {
	/*height: 99.5vh;*/
	/*width: 70vw;*/
	width:700px;
	/*margin: 0 auto;*/
	color:black;
	background-color: white;
	background-image: url(img/diamond_upholstery.png);
	/*opacity:0.9;*/
	border: 1px solid #000000;
	-webkit-box-shadow:0 0 10px rgba(0,0,0,.8);
	        box-shadow:0 0 10px rgba(0,0,0,.8);
	text-align:left;
	overflow:auto;
	padding:50px 20px 10px 20px;
	font-family: "Open Sans", Arial, Helvetica, sans-serif;
	font-size:1em;
		
	z-index:5;
	position:fixed;
	top:-1px;
	bottom:-1px;
	left:50%;
	-webkit-transform: translate(-50%, 0);
	    -ms-transform: translate(-50%, 0);
	        transform: translate(-50%, 0);
}

#scrollbuffer { visibility:hidden; margin-top:20px;}

#floater {
	width: 700px;
	z-index:6;
	position:fixed;
	top: 1vh;
	left:50%;
	-webkit-transform: translate(-50%, 0);
	    -ms-transform: translate(-50%, 0);
	        transform: translate(-50%, 0);
}

/* Position and sizing of clickable cross button */
.articleX {  /*.bm-cross-button*/
  height: 25px!important;
  width: 25px!important;
  /*background-color:red;*/
  background-color:#f44242;
  border-radius:3px;
  z-index:5;
  -webkit-box-shadow:4px 4px 8px rgba(200,0,0,3);
          box-shadow:4px 4px 8px rgba(200,0,0,3);
  /*top:50px!important;
  right:20px!important;*/
  /*position:fixed!important;*/
  left:50%;
  top:5px!important;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}


#header { display:none; }
#header h1, #header h3 { color:#c60000!important;  -webkit-margin-before: 0.3em;}

#markdown { margin-top:10px; }

#outerTOC {
	padding:15px 5px 10px 5px;
	border-radius:10px;
	opacity:0.85;
	text-align:left;
	background-color:white;
	z-index:5;
	width:220px;
	position:fixed;
	left:50%;
	-webkit-transform: translate(-605px, 0);
	    -ms-transform: translate(-605px, 0);
	        transform: translate(-605px, 0);
	top:30px;
	font-size:.95em;
}

.tryIt {
	float:right;
	background-color:green;
	color:white;
	font-weight:bold;
	border-radius: 10px;
	padding:13px;
	cursor:pointer;
	
    position: relative;
    display: inline-block;
}

/* Tooltip text */
.tryIt .tooltiptext {
    visibility:hidden;
    width: 130px;
    background-color: black;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
 
    /* Position the tooltip text - see examples below! */
    position: absolute;
    z-index: 5;
	
    top: -5px;
    right: 105%;
	
	font-weight:initial;
	font-size:.82em;
	font-family:sans-serif;
}

.tooltiptext p { margin:2px; }

.tryIt .tooltiptext::after {
    content: " ";
    position: absolute;
    top: 25%;
    left: 100%; /* To the right of the tooltip */
    margin-top: -5px;
    border-width: 10px;
    border-style: solid;
    border-color: transparent transparent transparent black;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tryIt:hover .tooltiptext {
    visibility: visible;
}


.inline {
	display:inline-block;
	padding-left: 10px;
}

/*#navigation { margin-top:-25px;}*/

#innerTOC { display:none; }

.TOCLink { cursor:pointer; color:#0f54c4; font-weight:bold; padding:7px;}
/*.TOCLink:hover { background-color: #cddaf4; border-radius:5px;}*/
.TOCLink:hover { border:1px solid #000000; padding:6px; border-radius:5px;}
.activeLink { background-color: #cddaf4; border-radius:5px; color:#0f54c4; font-weight:bold; padding:7px;}

.subtopic { color:#6065a0; font-weight:initial; list-style-type:square; padding:0px; margin:5px 0px 5px 20px; }

/*#centeredArticle {}*/

#article-body h1, #article-body h2, #article-body h3, #article-body h4, #article-body h5, #article-body h6 {
	font-family: "Lato";
	font-weight: bold;
	color: #0f54c4
}

#article-body h5, #article-body h6 {  -webkit-margin-after: 0em; }

#redspan { color:#c60000; font-weight:bold;}
.inlinepad { display:inline-block; padding-right: 20px;}

#article-body li {
	margin-bottom: 5px;
}


/*list elements*/
#article-body ul {
	list-style-type: none;
	padding: 0;
	margin: 0;  }
#article-body ul li {
	/*list-style-image: url(img/bullet.png);*/
    /*line-height: 1.125rem;*/  
	background-image: url(img/star.png);
    background-repeat: no-repeat;
    background-position: 0 0.1rem;
    padding-left: 1.75rem;
}


@media screen and (max-width:1220px) {
	#article-body, #floater { -webkit-transform:translate(-35%,0); -ms-transform:translate(-35%,0); transform:translate(-35%,0); }
	#outerTOC { -webkit-transform: translate(-490px,0); -ms-transform: translate(-490px,0); transform: translate(-490px,0); }
}

@media screen and (max-width:980px) {
	#article-body, #floater { width:500px; }
	#outerTOC { -webkit-transform: translate(-422px,0); -ms-transform: translate(-422px,0); transform: translate(-422px,0); }	
}

@media screen and (max-width:860px) {
	#article-body, #floater { -webkit-transform:translate(-28.5%,0); -ms-transform:translate(-28.5%,0); transform:translate(-28.5%,0) }
	#outerTOC { -webkit-transform: translate(-385px,0); -ms-transform: translate(-385px,0); transform: translate(-385px,0); }	
}

@media screen and (max-width:780px) {
	#article-body, #floater { -webkit-transform:translate(-50%,0); -ms-transform:translate(-50%,0); transform:translate(-50%,0) }
	#outerTOC { display:none; }
	#innerTOC { display:initial; }	
	#header { display:initial; }
	#scrollbuffer { visibility:visible; margin-top:50px; }
	#markdown { border-top: 1px dashed #000; }	
}


@media screen and (max-width:650px) {
	#article-body { left:initial!important; width:initial!important; -webkit-transform:initial!important; -ms-transform:initial!important; transform:initial!important; }
	#floater { left:initial!important; width:100%!important; -webkit-transform:initial!important; -ms-transform:initial!important; transform:initial!important; }
	img {max-width:100%;}
	#outerTOC { display:none!important; }	
	/*innerTOC and header already defined in max-width 780px*/
}

@media screen and (max-height:720px) {
	#outerTOC { top:0px; font-size: 0.9em; }
}

@media screen and (max-height:580px) {
	#innerTOC { display:initial; }
	#scrollbuffer { visibility:visible; margin-top:50px; }	
	#outerTOC { display:none; }
	#header { display:initial; }	
	#article-body, #floater {	left:50%; -webkit-transform: translate(-50%, 0); -ms-transform: translate(-50%, 0); transform: translate(-50%, 0); width:70%; }	
}


.hide {display:none;}

.center { text-align:center; }

#smallMargin { margin: 5px 0px 5px 0px; }
.smallText { font-size: .75em; line-height:1em; margin-top:-10px;}
.center { text-align:center; }

.floatleft, .floatright, .center img, .floatleft img, .floatright img {
	border-radius:7px; 
}

.floatleft {  float: left;  margin-right:10px; }
.floatright {  float: right;  margin-left:10px; }


@media (max-width: 450px)
{	img, .image, .floatright, .floatleft {
		float:none;
	}
}

.overlay {
	position: fixed;
	z-index: 3;
	width: 100%;
	height: 100%;
	top:0px;
	left:0px;
	background: rgba(0, 0, 0, 0.3);
	opacity: 1;
	/*cursor:pointer;*/
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}


